import React, { useState, useEffect } from 'react';
import {
  Box,
  Button,
  Typography,
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TableHead,
  TableRow,
  Paper,
  Chip,
} from '@mui/material';
import { Add, People } from '@mui/icons-material';
import { courseService } from '../services/courseService.jsx';
import { studentService } from '../services/studentService.jsx';
import CourseDialog from '../components/dialogs/CourseDialog.jsx';
import EnrollmentDialog from '../components/dialogs/EnrollmentDialog.jsx';
import Loading from '../components/Loading.jsx';
import ErrorMessage from '../components/ErrorMessage.jsx';
import { useNotification } from '../context/NotificationContext';

const Courses = () => {
  const { showNotification } = useNotification();
  const [courses, setCourses] = useState([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);
  const [dialogOpen, setDialogOpen] = useState(false);
  const [selectedCourse, setSelectedCourse] = useState();
  const [dialogMode, setDialogMode] = useState('add');
  const [enrollmentDialogOpen, setEnrollmentDialogOpen] = useState(false);
  const [selectedCourseForEnrollment, setSelectedCourseForEnrollment] = useState();
  const [enrolledStudents, setEnrolledStudents] = useState([]);
  const [availableStudents, setAvailableStudents] = useState([]);

  const loadCourses = async () => {
    try {
      setLoading(true);
      setError(null);
      const response = await courseService.getAll();
      setCourses(response.data);
    } catch (error) {
      console.error('Error loading courses:', error);
      setError('Failed to load courses. Please try again.');
      showNotification({
        type: 'error',
        message: 'Failed to load courses. Please try again.',
      });
    } finally {
      setLoading(false);
    }
  };

  useEffect(() => {
    loadCourses();
  }, []);

  const handleAddClick = () => {
    setSelectedCourse(undefined);
    setDialogMode('add');
    setDialogOpen(true);
  };

  const handleEditClick = (course) => {
    setSelectedCourse(course);
    setDialogMode('edit');
    setDialogOpen(true);
  };

  const handleDeleteCourse = async (id) => {
    if (window.confirm('Are you sure you want to delete this course?')) {
      try {
        await courseService.delete(id);
        await loadCourses();
        showNotification({
          type: 'success',
          message: 'Course deleted successfully',
        });
      } catch (error) {
        console.error('Error deleting course:', error);
        showNotification({
          type: 'error',
          message: 'Failed to delete course. Please try again.',
        });
      }
    }
  };

  const handleSaveCourse = async (course) => {
    try {
      if (dialogMode === 'add') {
        await courseService.create(course);
        showNotification({
          type: 'success',
          message: 'Course added successfully',
        });
      } else {
        await courseService.update(selectedCourse.id, course);
        showNotification({
          type: 'success',
          message: 'Course updated successfully',
        });
      }
      await loadCourses();
      setDialogOpen(false);
    } catch (error) {
      console.error('Error saving course:', error);
      showNotification({
        type: 'error',
        message: 'Failed to save course. Please try again.',
      });
      throw error;
    }
  };

  const handleEnrollmentClick = async (course) => {
    try {
      setSelectedCourseForEnrollment(course);
      const [enrolled, available] = await Promise.all([
        studentService.getAll(),
        studentService.getAll(),
      ]);
      const enrolledIds = new Set(Array.from({ length: course.enrolled }, (_, i) => i));
      setEnrolledStudents(enrolled.data.filter(s => enrolledIds.has(s.id % enrolled.data.length)));
      setAvailableStudents(enrolled.data.filter(s => !enrolledIds.has(s.id % enrolled.data.length)));
      setEnrollmentDialogOpen(true);
    } catch (error) {
      console.error('Error loading students for enrollment:', error);
      showNotification({
        type: 'error',
        message: 'Failed to load students for enrollment management.',
      });
    }
  };

  const handleEnroll = async (studentId, courseId) => {
    try {
      await courseService.enrollStudent(courseId, studentId);
      const student = availableStudents.find(s => s.id === studentId);
      setEnrolledStudents([...enrolledStudents, student]);
      setAvailableStudents(availableStudents.filter(s => s.id !== studentId));
      await loadCourses();
      showNotification({
        type: 'success',
        message: 'Student enrolled successfully.',
      });
    } catch (error) {
      console.error('Error enrolling student:', error);
      showNotification({
        type: 'error',
        message: 'Failed to enroll student. Please try again.',
      });
    }
  };

  const handleUnenroll = async (studentId, courseId) => {
    try {
      await courseService.unenrollStudent(courseId, studentId);
      const student = enrolledStudents.find(s => s.id === studentId);
      setAvailableStudents([...availableStudents, student]);
      setEnrolledStudents(enrolledStudents.filter(s => s.id !== studentId));
      await loadCourses();
      showNotification({
        type: 'success',
        message: 'Student unenrolled successfully.',
      });
    } catch (error) {
      console.error('Error unenrolling student:', error);
      showNotification({
        type: 'error',
        message: 'Failed to unenroll student. Please try again.',
      });
    }
  };

  const getAvailabilityColor = (enrolled, capacity) => {
    const ratio = enrolled / capacity;
    if (ratio >= 1) return 'error';
    if (ratio >= 0.8) return 'warning';
    return 'success';
  };

  if (loading) {
    return <Loading message="Loading courses..." />;
  }

  if (error) {
    return <ErrorMessage message={error} onRetry={loadCourses} />;
  }

  return (
    <Box>
      <Box sx={{ display: 'flex', justifyContent: 'space-between', mb: 4 }}>
        <Typography variant="h4">Courses</Typography>
        <Button
          variant="contained"
          startIcon={<Add />}
          onClick={handleAddClick}
        >
          Add Course
        </Button>
      </Box>

      <TableContainer component={Paper}>
        <Table>
          <TableHead>
            <TableRow>
              <TableCell>ID</TableCell>
              <TableCell>Name</TableCell>
              <TableCell>Department</TableCell>
              <TableCell>Teacher</TableCell>
              <TableCell>Credits</TableCell>
              <TableCell>Availability</TableCell>
              <TableCell>Actions</TableCell>
            </TableRow>
          </TableHead>
          <TableBody>
            {courses.map((course) => (
              <TableRow key={course.id}>
                <TableCell>{course.id}</TableCell>
                <TableCell>{course.name}</TableCell>
                <TableCell>{course.department}</TableCell>
                <TableCell>{course.teacher}</TableCell>
                <TableCell>{course.credits}</TableCell>
                <TableCell>
                  <Chip
                    label={`${course.enrolled}/${course.capacity}`}
                    color={getAvailabilityColor(course.enrolled, course.capacity)}
                    size="small"
                  />
                </TableCell>
                <TableCell>
                  <Button size="small" onClick={() => handleEditClick(course)}>
                    Edit
                  </Button>
                  <Button
                    size="small"
                    color="error"
                    onClick={() => handleDeleteCourse(course.id)}
                  >
                    Delete
                  </Button>
                  <Button
                    size="small"
                    startIcon={<People />}
                    onClick={() => handleEnrollmentClick(course)}
                  >
                    Manage Students
                  </Button>
                </TableCell>
              </TableRow>
            ))}
          </TableBody>
        </Table>
      </TableContainer>

      <CourseDialog
        open={dialogOpen}
        onClose={() => setDialogOpen(false)}
        onSave={handleSaveCourse}
        course={selectedCourse}
        mode={dialogMode}
      />

      <EnrollmentDialog
        open={enrollmentDialogOpen}
        onClose={() => setEnrollmentDialogOpen(false)}
        onEnroll={handleEnroll}
        onUnenroll={handleUnenroll}
        course={selectedCourseForEnrollment}
        enrolledStudents={enrolledStudents}
        availableStudents={availableStudents}
      />
    </Box>
  );
};

export default Courses;