const development = {
  apiUrl: 'http://localhost:8080',
  fallbackApiUrl: 'http://localhost:3000', // Fallback URL in case primary fails
  swaggerUrl: 'http://localhost:8080/swagger-ui/index.html',
  apiTimeout: 10000, // 10 second timeout for API calls
  retryAttempts: 2 // Number of times to retry failed requests
};

const production = {
  apiUrl: import.meta.env.VITE_API_URL || 'https://api.schoolms.com',
  fallbackApiUrl: import.meta.env.VITE_FALLBACK_API_URL || 'https://backup-api.schoolms.com',
  swaggerUrl: import.meta.env.VITE_SWAGGER_URL || 'https://api.schoolms.com/swagger-ui/index.html',
  apiTimeout: 15000, // 15 second timeout for API calls in production
  retryAttempts: 3
};

const config = import.meta.env.MODE === 'production' ? production : development;

export default config;