import React, { useState, useEffect } from 'react';
import {
  Box,
  Typography,
  Button,
  Dialog,
  DialogTitle,
  DialogContent,
  DialogActions,
  CircularProgress,
  List,
  ListItem,
  ListItemText,
  Divider,
  Paper,
  Alert,
  Switch,
  FormControlLabel,
  TextField,
} from '@mui/material';
import { Add } from '@mui/icons-material';
import { studentService } from '../services/studentService.jsx';
import StudentDialog from '../components/dialogs/StudentDialog.jsx';
import Loading from '../components/Loading.jsx';
import ErrorMessage from '../components/ErrorMessage.jsx';
import { useNotification } from '../context/NotificationContext';
import DataTable from '../components/DataTable.jsx';
import { useAuth } from '../context/AuthContext';
import api from '../services/api.jsx';
import environment from '../config/environment.jsx';

const Students = () => {
  const { showNotification } = useNotification();
  const { user } = useAuth();
  const [students, setStudents] = useState([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);
  const [dialogOpen, setDialogOpen] = useState(false);
  const [selectedStudent, setSelectedStudent] = useState();
  const [dialogMode, setDialogMode] = useState('add');
  const [debugDialogOpen, setDebugDialogOpen] = useState(false);
  const [debugLoading, setDebugLoading] = useState(false);
  const [debugResults, setDebugResults] = useState([]);
  const [errorDetails, setErrorDetails] = useState(null);
  const [useMockData, setUseMockData] = useState(false);
  const [customEndpoint, setCustomEndpoint] = useState('');
  const [customEndpointResult, setCustomEndpointResult] = useState(null);

  // Debug logging on component mount
  useEffect(() => {
    console.log(' Students component mounted');
    console.log(' User:', user);
    
    const token = localStorage.getItem('token');
    if (token) {
      try {
        const tokenData = JSON.parse(atob(token.split('.')[1]));
        console.log(' Token payload:', {
          exp: new Date(tokenData.exp * 1000).toLocaleString(),
          roles: tokenData.roles || tokenData.authorities || tokenData.role || 'Not found in token',
          sub: tokenData.sub || tokenData.username
        });
      } catch (e) {
        console.log(' Could not parse token');
      }
    }
    
    // Check for saved endpoint pattern
    const savedPattern = localStorage.getItem('studentApiPattern');
    if (savedPattern) {
      console.log(' Found saved student API pattern:', savedPattern);
    }
    
    return () => {
      console.log(' Students component unmounted');
    };
  }, [user]);

  const columns = [
    { id: 'studentId', label: 'ID', sortable: true },
    { id: 'name', label: 'Name', sortable: true },
    { id: 'grade', label: 'Grade', sortable: true },
    { id: 'email', label: 'Email', sortable: true },
    { id: 'phoneNumber', label: 'Phone Number', sortable: true },
    {
      id: 'actions',
      label: 'Actions',
      format: (value) => {
        return (
          <Box>
            <Button size="small" onClick={() => handleEditClick(value)}>
              Edit
            </Button>
            <Button
              size="small"
              color="error"
              onClick={() => handleDeleteStudent(value.id)}
            >
              Delete
            </Button>
          </Box>
        );
      },
    },
  ];

  const loadStudents = async () => {
    try {
      console.log(' Loading students...');
      setLoading(true);
      setError(null);
      setErrorDetails(null);
      
      // Log user and auth state
      console.log(' Current user role:', user?.role);
      console.log(' Auth token present:', !!localStorage.getItem('token'));
      
      let response;
      
      if (useMockData) {
        // Use mock data if selected
        console.log(' Using mock student data');
        response = studentService.getMockStudents();
        showNotification({
          type: 'info',
          message: 'Using mock student data (real API is disabled)',
        });
      } else {
        // Try real API
        response = await studentService.getAll();
      }
      
      console.log(' Students loaded successfully:', response.data);
      setStudents(response.data);
    } catch (error) {
      console.error(' Error loading students:', error);
      setError('Failed to load students. Please try again or use mock data.');
      setErrorDetails(error);
      showNotification({
        type: 'error',
        message: 'Failed to load students. Please try again or enable mock data.',
      });
    } finally {
      setLoading(false);
    }
  };

  useEffect(() => {
    loadStudents();
  }, [useMockData]); // Reload when mock data setting changes

  const handleAddClick = () => {
    setSelectedStudent(undefined);
    setDialogMode('add');
    setDialogOpen(true);
  };

  const handleEditClick = (student) => {
    setSelectedStudent(student);
    setDialogMode('edit');
    setDialogOpen(true);
  };

  const handleDeleteStudent = async (id) => {
    if (window.confirm('Are you sure you want to delete this student?')) {
      try {
        if (useMockData) {
          // Simulate delete in mock data mode
          setStudents(students.filter(student => student.id !== id));
          showNotification({
            type: 'success',
            message: 'Student deleted successfully (mock)',
          });
        } else {
          await studentService.delete(id);
          await loadStudents();
          showNotification({
            type: 'success',
            message: 'Student deleted successfully',
          });
        }
      } catch (error) {
        console.error('Error deleting student:', error);
        setErrorDetails(error);
        showNotification({
          type: 'error',
          message: 'Failed to delete student. Please try again.',
        });
      }
    }
  };

  const handleSaveStudent = async (student) => {
    try {
      if (useMockData) {
        // Simulate save in mock data mode
        if (dialogMode === 'add') {
          const newStudent = {
            ...student,
            id: Math.floor(Math.random() * 10000),
            studentId: `S${Math.floor(Math.random() * 10000).toString().padStart(4, '0')}`,
          };
          setStudents([...students, newStudent]);
          showNotification({
            type: 'success',
            message: 'Student added successfully (mock)',
          });
        } else {
          setStudents(students.map(s => s.id === selectedStudent.id ? {...student, id: selectedStudent.id} : s));
          showNotification({
            type: 'success',
            message: 'Student updated successfully (mock)',
          });
        }
        setDialogOpen(false);
        return;
      }
      
      if (dialogMode === 'add') {
        await studentService.create(student);
        showNotification({
          type: 'success',
          message: 'Student added successfully',
        });
      } else {
        await studentService.update(selectedStudent.id, student);
        showNotification({
          type: 'success',
          message: 'Student updated successfully',
        });
      }
      await loadStudents();
      setDialogOpen(false);
    } catch (error) {
      console.error('Error saving student:', error);
      setErrorDetails(error);
      showNotification({
        type: 'error',
        message: 'Failed to save student. Please try again.',
      });
      throw error;
    }
  };

  // Helper function to run comprehensive API diagnostics
  const runApiTests = async () => {
    setDebugLoading(true);
    setDebugResults([]);
    
    try {
      const results = await studentService.runEndpointTests();
      setDebugResults(results);
      
      // Check if any endpoints succeeded
      const successful = results.filter(r => r.success);
      if (successful.length > 0) {
        showNotification({
          type: 'success',
          message: `Found ${successful.length} working endpoint(s). Using ${successful[0].endpoint}.`
        });
      } else {
        showNotification({
          type: 'warning',
          message: 'No working endpoints found. Consider using mock data.'
        });
      }
    } catch (error) {
      console.error('Error running API tests:', error);
      showNotification({
        type: 'error',
        message: 'Error running API tests'
      });
    }
    
    setDebugLoading(false);
  };
  
  // Test a custom endpoint
  const testCustomEndpoint = async () => {
    if (!customEndpoint) return;
    
    setDebugLoading(true);
    
    try {
      const response = await api.get(customEndpoint);
      setCustomEndpointResult({
        success: true,
        status: response.status,
        data: response.data
      });
      
      // Save this as the working endpoint
      localStorage.setItem('studentApiPattern', customEndpoint);
      showNotification({
        type: 'success',
        message: `Endpoint ${customEndpoint} works! Saved as default.`
      });
    } catch (error) {
      setCustomEndpointResult({
        success: false,
        status: error.status || (error.response && error.response.status) || 'Error',
        message: error.message
      });
      showNotification({
        type: 'error',
        message: `Endpoint ${customEndpoint} failed.`
      });
    }
    
    setDebugLoading(false);
  };

  // Debug dialog component
  const renderDebugDialog = () => {
    return (
      <Dialog 
        open={debugDialogOpen} 
        onClose={() => setDebugDialogOpen(false)}
        maxWidth="md"
        fullWidth
      >
        <DialogTitle>Student API Diagnostics</DialogTitle>
        <DialogContent>
          <Typography variant="body2" gutterBottom>
            This tool helps diagnose issues with connecting to your student API endpoints.
          </Typography>
          
          <Box sx={{ my: 3, display: 'flex', alignItems: 'center', justifyContent: 'space-between' }}>
            <FormControlLabel
              control={
                <Switch
                  checked={useMockData}
                  onChange={(e) => setUseMockData(e.target.checked)}
                />
              }
              label="Use mock data (bypass API)"
            />
            
            <Button
              onClick={runApiTests}
              disabled={debugLoading}
              variant="contained"
              color="primary"
            >
              {debugLoading ? 'Testing...' : 'Test All Endpoints'}
            </Button>
          </Box>
          
          <Box sx={{ mt: 2 }}>
            <Typography variant="subtitle2" gutterBottom>Test Custom Endpoint:</Typography>
            <Box sx={{ display: 'flex', alignItems: 'center', gap: 1, mb: 2 }}>
              <TextField
                fullWidth
                size="small"
                value={customEndpoint}
                onChange={(e) => setCustomEndpoint(e.target.value)}
                placeholder="e.g., /api/custom-students"
                variant="outlined"
              />
              <Button 
                onClick={testCustomEndpoint} 
                disabled={!customEndpoint || debugLoading}
                variant="outlined"
              >
                Test
              </Button>
            </Box>
            
            {customEndpointResult && (
              <Paper variant="outlined" sx={{ p: 2, mb: 3 }}>
                <Typography variant="subtitle2" color={customEndpointResult.success ? 'success.main' : 'error.main'}>
                  {customEndpointResult.success ? ' Success' : ' Failed'} - Status: {customEndpointResult.status}
                </Typography>
                <Box 
                  component="pre" 
                  sx={{ 
                    mt: 1, 
                    p: 1, 
                    bgcolor: 'grey.100', 
                    borderRadius: 1,
                    fontSize: '0.75rem',
                    overflow: 'auto',
                    maxHeight: 150
                  }}
                >
                  {customEndpointResult.success 
                    ? JSON.stringify(customEndpointResult.data, null, 2)
                    : customEndpointResult.message
                  }
                </Box>
              </Paper>
            )}
          </Box>
          
          {debugLoading && (
            <Box sx={{ display: 'flex', justifyContent: 'center', my: 4 }}>
              <CircularProgress />
            </Box>
          )}
          
          {debugResults.length > 0 && (
            <>
              <Typography variant="h6" gutterBottom>Endpoint Test Results:</Typography>
              <List>
                {debugResults.map((result, index) => (
                  <React.Fragment key={index}>
                    {index > 0 && <Divider />}
                    <ListItem>
                      <ListItemText 
                        primary={`${result.endpoint} - ${result.success ? ' Success' : ' Failed'} (${result.status})`}
                        secondary={
                          <Box 
                            component="pre" 
                            sx={{ 
                              mt: 1, 
                              p: 1, 
                              bgcolor: 'grey.100', 
                              borderRadius: 1,
                              fontSize: '0.75rem',
                              overflow: 'auto',
                              maxHeight: 150
                            }}
                          >
                            {result.success 
                              ? JSON.stringify(result.data, null, 2)
                              : result.message || 'No response data'
                            }
                          </Box>
                        }
                      />
                    </ListItem>
                  </React.Fragment>
                ))}
              </List>
            </>
          )}

          <Box sx={{ mt: 4 }}>
            <Typography variant="h6" gutterBottom>Authentication Information</Typography>
            <Paper sx={{ p: 2 }}>
              <Typography variant="body2">
                <strong>User:</strong> {user ? JSON.stringify(user) : 'Not logged in'}
              </Typography>
              <Typography variant="body2">
                <strong>Token present:</strong> {localStorage.getItem('token') ? 'Yes' : 'No'}
              </Typography>
              <Typography variant="body2">
                <strong>API URL:</strong> {environment.apiUrl}
              </Typography>
              <Typography variant="body2">
                <strong>Saved endpoint:</strong> {localStorage.getItem('studentApiPattern') || 'None'}
              </Typography>
            </Paper>
          </Box>
        </DialogContent>
        <DialogActions>
          <Button onClick={() => {
            localStorage.removeItem('studentApiPattern');
            showNotification({
              type: 'info',
              message: 'Cleared saved endpoint pattern'
            });
          }} color="warning">
            Clear Saved Endpoint
          </Button>
          <Button onClick={() => setDebugDialogOpen(false)} color="secondary">
            Close
          </Button>
        </DialogActions>
      </Dialog>
    );
  };

  if (loading) {
    return <Loading message="Loading students..." />;
  }

  return (
    <Box sx={{ p: 3 }}>
      <Box sx={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center', mb: 4 }}>
        <Typography variant="h4">Students</Typography>
        <Box>
          <Button 
            variant="text"
            color="secondary"
            onClick={() => setDebugDialogOpen(true)}
            sx={{ mr: 2 }}
          >
            Debug
          </Button>
          {useMockData && (
            <Button
              variant="outlined"
              color="warning"
              onClick={() => setUseMockData(false)}
              sx={{ mr: 2 }}
            >
              Using Mock Data
            </Button>
          )}
          <Button
            variant="contained"
            startIcon={<Add />}
            onClick={handleAddClick}
          >
            Add Student
          </Button>
        </Box>
      </Box>

      {error && (
        <Box sx={{ mb: 4 }}>
          <ErrorMessage message={error} onRetry={loadStudents} />
          
          <Paper elevation={0} variant="outlined" sx={{ p: 3, mt: 2 }}>
            <Alert severity="info" sx={{ mb: 2 }}>
              If you're experiencing persistent API errors, you can use mock data instead.
            </Alert>
            
            <Button
              variant="contained"
              color="secondary"
              onClick={() => setUseMockData(true)}
              sx={{ mr: 2 }}
            >
              Enable Mock Data
            </Button>
            <Button
              variant="outlined"
              onClick={() => setDebugDialogOpen(true)}
            >
              Run Diagnostics
            </Button>
          </Paper>
        </Box>
      )}

      <Paper sx={{ mb: 4 }}>
        <DataTable
          columns={columns}
          data={students}
          defaultRowsPerPage={10}
          rowsPerPageOptions={[5, 10, 25, 50]}
        />
      </Paper>

      <StudentDialog
        open={dialogOpen}
        onClose={() => setDialogOpen(false)}
        onSave={handleSaveStudent}
        student={selectedStudent}
        mode={dialogMode}
      />
      
      {renderDebugDialog()}
    </Box>
  );
};

export default Students;