import React, { useState, useEffect } from 'react';
import {
  Box,
  Typography,
  Button,
} from '@mui/material';
import { Add } from '@mui/icons-material';
import { teacherService } from '../services/teacherService.jsx';
import TeacherDialog from '../components/dialogs/TeacherDialog.jsx';
import Loading from '../components/Loading.jsx';
import ErrorMessage from '../components/ErrorMessage.jsx';
import { useNotification } from '../context/NotificationContext';
import DataTable from '../components/DataTable.jsx';

const Teachers = () => {
  const { showNotification } = useNotification();
  const [teachers, setTeachers] = useState([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);
  const [dialogOpen, setDialogOpen] = useState(false);
  const [selectedTeacher, setSelectedTeacher] = useState();
  const [dialogMode, setDialogMode] = useState('add');

  const columns = [
    { id: 'id', label: 'ID', sortable: true },
    { id: 'name', label: 'Name', sortable: true },
    { id: 'department', label: 'Department', sortable: true },
    { id: 'subject', label: 'Subject', sortable: true },
    { id: 'email', label: 'Email', sortable: true },
    { id: 'phoneNumber', label: 'Phone Number', sortable: true },
    {
      id: 'actions',
      label: 'Actions',
      format: (value) => {
        return (
          <Box>
            <Button size="small" onClick={() => handleEditClick(value)}>
              Edit
            </Button>
            <Button
              size="small"
              color="error"
              onClick={() => handleDeleteTeacher(value.id)}
            >
              Delete
            </Button>
          </Box>
        );
      },
    },
  ];

  const loadTeachers = async () => {
    try {
      setLoading(true);
      setError(null);
      const response = await teacherService.getAll();
      setTeachers(response.data);
    } catch (error) {
      console.error('Error loading teachers:', error);
      setError('Failed to load teachers. Please try again.');
      showNotification({
        type: 'error',
        message: 'Failed to load teachers. Please try again.',
      });
    } finally {
      setLoading(false);
    }
  };

  useEffect(() => {
    loadTeachers();
  }, []);

  const handleAddClick = () => {
    setSelectedTeacher(undefined);
    setDialogMode('add');
    setDialogOpen(true);
  };

  const handleEditClick = (teacher) => {
    setSelectedTeacher(teacher);
    setDialogMode('edit');
    setDialogOpen(true);
  };

  const handleDeleteTeacher = async (id) => {
    if (window.confirm('Are you sure you want to delete this teacher?')) {
      try {
        await teacherService.delete(id);
        await loadTeachers();
        showNotification({
          type: 'success',
          message: 'Teacher deleted successfully',
        });
      } catch (error) {
        console.error('Error deleting teacher:', error);
        showNotification({
          type: 'error',
          message: 'Failed to delete teacher. Please try again.',
        });
      }
    }
  };

  const handleSaveTeacher = async (teacher) => {
    try {
      if (dialogMode === 'add') {
        await teacherService.create(teacher);
        showNotification({
          type: 'success',
          message: 'Teacher added successfully',
        });
      } else {
        await teacherService.update(selectedTeacher.id, teacher);
        showNotification({
          type: 'success',
          message: 'Teacher updated successfully',
        });
      }
      await loadTeachers();
      setDialogOpen(false);
    } catch (error) {
      console.error('Error saving teacher:', error);
      showNotification({
        type: 'error',
        message: 'Failed to save teacher. Please try again.',
      });
      throw error;
    }
  };

  if (loading) {
    return <Loading message="Loading teachers..." />;
  }

  if (error) {
    return <ErrorMessage message={error} onRetry={loadTeachers} />;
  }

  return (
    <Box>
      <Box sx={{ display: 'flex', justifyContent: 'space-between', mb: 4 }}>
        <Typography variant="h4">Teachers</Typography>
        <Button
          variant="contained"
          startIcon={<Add />}
          onClick={handleAddClick}
        >
          Add Teacher
        </Button>
      </Box>

      <DataTable
        columns={columns}
        data={teachers}
        defaultRowsPerPage={10}
        rowsPerPageOptions={[5, 10, 25, 50]}
      />

      <TeacherDialog
        open={dialogOpen}
        onClose={() => setDialogOpen(false)}
        onSave={handleSaveTeacher}
        teacher={selectedTeacher}
        mode={dialogMode}
      />
    </Box>
  );
};

export default Teachers;