import React, { useState, useMemo } from 'react';
import {
  Box,
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TableHead,
  TablePagination,
  TableRow,
  TableSortLabel,
  Paper,
  TextField,
  IconButton,
  Collapse,
  Typography,
  Grid,
  FormControl,
  InputLabel,
  MenuItem,
  Select,
} from '@mui/material';
import { FilterList } from '@mui/icons-material';

const DataTable = ({
  columns,
  data,
  rowsPerPageOptions = [5, 10, 25],
  defaultRowsPerPage = 10,
}) => {
  const [page, setPage] = useState(0);
  const [rowsPerPage, setRowsPerPage] = useState(defaultRowsPerPage);
  const [orderBy, setOrderBy] = useState('');
  const [order, setOrder] = useState('asc');
  const [filters, setFilters] = useState([]);
  const [showFilters, setShowFilters] = useState(false);
  const [searchQuery, setSearchQuery] = useState('');

  const handleChangePage = (_event, newPage) => {
    setPage(newPage);
  };

  const handleChangeRowsPerPage = (event) => {
    setRowsPerPage(parseInt(event.target.value, 10));
    setPage(0);
  };

  const handleSort = (property) => {
    const isAsc = orderBy === property && order === 'asc';
    setOrder(isAsc ? 'desc' : 'asc');
    setOrderBy(property);
  };

  const handleFilterChange = (field, value) => {
    setFilters(prev => {
      const existing = prev.find(f => f.field === field);
      if (existing) {
        return prev.map(f => f.field === field ? { ...f, value } : f);
      }
      return [...prev, { field, value }];
    });
    setPage(0);
  };

  const filteredAndSortedData = useMemo(() => {
    let result = [...data];

    // Apply search query
    if (searchQuery) {
      const searchLower = searchQuery.toLowerCase();
      result = result.filter(row => columns.some(column => {
        if (column.id === 'actions') return false;
        const value = row[column.id];
        return value != null && value.toString().toLowerCase().includes(searchLower);
      }));
    }

    // Apply filters
    filters.forEach(filter => {
      if (filter.value) {
        result = result.filter(row => {
          const value = row[filter.field];
          return value != null && value.toString().toLowerCase().includes(filter.value.toLowerCase());
        });
      }
    });

    // Apply sorting
    if (orderBy) {
      result.sort((a, b) => {
        const aValue = a[orderBy];
        const bValue = b[orderBy];
        if (aValue === bValue) return 0;
        if (aValue === null || aValue === undefined) return 1;
        if (bValue === null || bValue === undefined) return -1;
        const comparison = aValue < bValue ? -1 : 1;
        return order === 'desc' ? -comparison : comparison;
      });
    }

    return result;
  }, [data, searchQuery, filters, orderBy, order]);

  const paginatedData = filteredAndSortedData.slice(page * rowsPerPage, page * rowsPerPage + rowsPerPage);

  return (
    <Box>
      <Box sx={{ mb: 2 }}>
        <Grid container spacing={2} alignItems="center">
          <Grid item xs>
            <TextField
              fullWidth
              label="Search"
              value={searchQuery}
              onChange={(e) => setSearchQuery(e.target.value)}
              size="small"
            />
          </Grid>
          <Grid item>
            <IconButton onClick={() => setShowFilters(!showFilters)}>
              <FilterList color={filters.length > 0 ? "primary" : "inherit"} />
            </IconButton>
          </Grid>
        </Grid>
      </Box>

      <Collapse in={showFilters}>
        <Box sx={{ mb: 2 }}>
          <Typography variant="subtitle2" sx={{ mb: 1 }}>Filters</Typography>
          <Grid container spacing={2}>
            {columns.map(column => {
              if (!column.filterable || column.id === 'actions') return null;

              return (
                <Grid item xs={12} sm={6} md={4} key={String(column.id)}>
                  {column.filterOptions ? (
                    <FormControl fullWidth size="small">
                      <InputLabel>{column.label}</InputLabel>
                      <Select
                        value={filters.find(f => f.field === column.id)?.value || ''}
                        onChange={(e) => handleFilterChange(String(column.id), e.target.value)}
                        label={column.label}
                      >
                        <MenuItem value="">All</MenuItem>
                        {column.filterOptions.map(option => (
                          <MenuItem key={option} value={option}>
                            {option}
                          </MenuItem>
                        ))}
                      </Select>
                    </FormControl>
                  ) : (
                    <TextField
                      fullWidth
                      label={column.label}
                      size="small"
                      value={filters.find(f => f.field === column.id)?.value || ''}
                      onChange={(e) => handleFilterChange(String(column.id), e.target.value)}
                    />
                  )}
                </Grid>
              );
            })}
          </Grid>
        </Box>
      </Collapse>

      <Paper sx={{ width: '100%', overflow: 'hidden' }}>
        <TableContainer>
          <Table stickyHeader>
            <TableHead>
              <TableRow>
                {columns.map((column) => (
                  <TableCell key={String(column.id)} style={{ minWidth: column.minWidth }}>
                    {column.sortable ? (
                      <TableSortLabel
                        active={orderBy === column.id}
                        direction={orderBy === column.id ? order : 'asc'}
                        onClick={() => handleSort(column.id)}
                      >
                        {column.label}
                      </TableSortLabel>
                    ) : (
                      column.label
                    )}
                  </TableCell>
                ))}
              </TableRow>
            </TableHead>
            <TableBody>
              {paginatedData.map((row, index) => (
                <TableRow hover tabIndex={-1} key={row.id || index}>
                  {columns.map((column) => {
                    const value = column.id === 'actions' ? null : row[column.id];
                    return (
                      <TableCell key={String(column.id)}>
                        {column.format ? column.format(value) : value}
                      </TableCell>
                    );
                  })}
                </TableRow>
              ))}
            </TableBody>
          </Table>
        </TableContainer>
        <Box sx={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center', px: 2 }}>
          <Typography variant="body2" color="text.secondary">
            {filteredAndSortedData.length} total results
          </Typography>
          <TablePagination
            rowsPerPageOptions={rowsPerPageOptions}
            component="div"
            count={filteredAndSortedData.length}
            rowsPerPage={rowsPerPage}
            page={page}
            onPageChange={handleChangePage}
            onRowsPerPageChange={handleChangeRowsPerPage}
          />
        </Box>
      </Paper>
    </Box>
  );
};

export default DataTable;