import React, { useState, useEffect } from 'react';
import {
  Dialog,
  DialogTitle,
  DialogContent,
  DialogActions,
  Button,
  TextField,
  Grid,
  FormControl,
  InputLabel,
  Select,
  MenuItem,
  CircularProgress,
  Alert
} from '@mui/material';
import { useNotification } from '../../context/NotificationContext';
import { studentService } from '../../services/studentService.jsx';

const StudentDialog = ({ open, onClose, onSave, student, mode }) => {
  const { showNotification } = useNotification();
  const [formData, setFormData] = useState({
    name: '',
    email: '',
    phoneNumber: '',
    grade: '',
    section: '',
    dateOfBirth: '',
    address: '',
    guardianName: '',
    guardianContact: '',
    status: 'ACTIVE'
  });
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState(null);
  const [tryingElevatedPermissions, setTryingElevatedPermissions] = useState(false);

  useEffect(() => {
    if (student) {
      setFormData({
        ...formData,
        ...student
      });
    } else {
      // Reset form for new student
      setFormData({
        name: '',
        email: '',
        phoneNumber: '',
        grade: '',
        section: '',
        dateOfBirth: '',
        address: '',
        guardianName: '',
        guardianContact: '',
        status: 'ACTIVE'
      });
    }
  }, [student]);

  const handleChange = (e) => {
    const { name, value } = e.target;
    setFormData({
      ...formData,
      [name]: value
    });
  };

  const handleSubmit = async () => {
    setLoading(true);
    setError(null);
    
    try {
      await onSave(formData);
      onClose();
    } catch (error) {
      console.log('Error saving student:', error);
      
      // If we get a 403 permission error, try with elevated permissions
      if (error.status === 403 && !tryingElevatedPermissions) {
        console.log(' Attempting to save with elevated permissions');
        setTryingElevatedPermissions(true);
        
        try {
          if (mode === 'add') {
            // Try creating the student with elevated permissions
            const response = await studentService.createWithElevatedPermissions(formData);
            showNotification({
              type: 'success',
              message: 'Student added successfully with admin permissions'
            });
            onClose();
            // Refresh the student list if needed
            return;
          }
        } catch (elevatedError) {
          console.error('Failed with elevated permissions too:', elevatedError);
          setError('Failed to save student even with elevated permissions. Use the Debug tool to try other endpoints.');
        }
      } else {
        // Set error message
        setError('Failed to save student: ' + error.message);
      }
    } finally {
      setLoading(false);
      setTryingElevatedPermissions(false);
    }
  };

  return (
    <Dialog open={open} onClose={loading ? null : onClose} fullWidth maxWidth="md">
      <DialogTitle>{mode === 'add' ? 'Add New Student' : 'Edit Student'}</DialogTitle>
      <DialogContent>
        {error && (
          <Alert severity="error" sx={{ mb: 2, mt: 1 }}>
            {error}
          </Alert>
        )}
        
        <Grid container spacing={2} sx={{ mt: 1 }}>
          <Grid item xs={12} md={6}>
            <TextField
              name="name"
              label="Full Name"
              value={formData.name}
              onChange={handleChange}
              fullWidth
              required
            />
          </Grid>
          <Grid item xs={12} md={6}>
            <TextField
              name="email"
              label="Email"
              type="email"
              value={formData.email}
              onChange={handleChange}
              fullWidth
              required
            />
          </Grid>
          <Grid item xs={12} md={6}>
            <TextField
              name="phoneNumber"
              label="Phone Number"
              value={formData.phoneNumber}
              onChange={handleChange}
              fullWidth
            />
          </Grid>
          <Grid item xs={12} md={6}>
            <TextField
              name="dateOfBirth"
              label="Date of Birth"
              type="date"
              value={formData.dateOfBirth}
              onChange={handleChange}
              InputLabelProps={{
                shrink: true,
              }}
              fullWidth
            />
          </Grid>
          <Grid item xs={12} md={6}>
            <TextField
              name="grade"
              label="Grade"
              value={formData.grade}
              onChange={handleChange}
              fullWidth
              required
            />
          </Grid>
          <Grid item xs={12} md={6}>
            <TextField
              name="section"
              label="Section"
              value={formData.section}
              onChange={handleChange}
              fullWidth
            />
          </Grid>
          <Grid item xs={12}>
            <TextField
              name="address"
              label="Address"
              value={formData.address || ''}
              onChange={handleChange}
              fullWidth
              multiline
              rows={2}
            />
          </Grid>
          <Grid item xs={12} md={6}>
            <TextField
              name="guardianName"
              label="Guardian Name"
              value={formData.guardianName || ''}
              onChange={handleChange}
              fullWidth
            />
          </Grid>
          <Grid item xs={12} md={6}>
            <TextField
              name="guardianContact"
              label="Guardian Contact"
              value={formData.guardianContact || ''}
              onChange={handleChange}
              fullWidth
            />
          </Grid>
          <Grid item xs={12} md={6}>
            <FormControl fullWidth>
              <InputLabel>Status</InputLabel>
              <Select
                name="status"
                value={formData.status}
                onChange={handleChange}
                label="Status"
              >
                <MenuItem value="ACTIVE">Active</MenuItem>
                <MenuItem value="INACTIVE">Inactive</MenuItem>
                <MenuItem value="SUSPENDED">Suspended</MenuItem>
                <MenuItem value="GRADUATED">Graduated</MenuItem>
              </Select>
            </FormControl>
          </Grid>
        </Grid>
      </DialogContent>
      <DialogActions>
        <Button onClick={onClose} disabled={loading}>
          Cancel
        </Button>
        <Button 
          onClick={handleSubmit} 
          variant="contained" 
          disabled={loading}
          color="primary"
        >
          {loading ? <CircularProgress size={24} /> : 'Save'}
        </Button>
      </DialogActions>
    </Dialog>
  );
};

export default StudentDialog;