import React, { useState, useEffect } from 'react';
import {
  Dialog,
  DialogTitle,
  DialogContent,
  DialogActions,
  Button,
  TextField,
  List,
  ListItem,
  ListItemText,
  ListItemSecondaryAction,
  IconButton,
  Box,
  Typography,
  Chip,
} from '@mui/material';
import { Add, Remove } from '@mui/icons-material';

const EnrollmentDialog = ({
  open,
  onClose,
  onEnroll,
  onUnenroll,
  course,
  enrolledStudents,
  availableStudents,
}) => {
  const [searchTerm, setSearchTerm] = useState('');
  const [isProcessing, setIsProcessing] = useState(false);

  useEffect(() => {
    setSearchTerm('');
  }, [open]);

  const filteredAvailableStudents = availableStudents.filter(
    student =>
      student.name.toLowerCase().includes(searchTerm.toLowerCase()) ||
      student.grade.toLowerCase().includes(searchTerm.toLowerCase())
  );

  const filteredEnrolledStudents = enrolledStudents.filter(
    student =>
      student.name.toLowerCase().includes(searchTerm.toLowerCase()) ||
      student.grade.toLowerCase().includes(searchTerm.toLowerCase())
  );

  const handleEnroll = async (studentId) => {
    if (!course) return;
    try {
      setIsProcessing(true);
      await onEnroll(studentId, course.id);
    } finally {
      setIsProcessing(false);
    }
  };

  const handleUnenroll = async (studentId) => {
    if (!course) return;
    try {
      setIsProcessing(true);
      await onUnenroll(studentId, course.id);
    } finally {
      setIsProcessing(false);
    }
  };

  return (
    <Dialog open={open} onClose={onClose} maxWidth="sm" fullWidth>
      <DialogTitle>
        Manage Enrollments - {course?.name}
        <Chip
          label={`${enrolledStudents.length}/${course?.capacity || 0}`}
          color={enrolledStudents.length >= (course?.capacity || 0) ? 'error' : 'success'}
          size="small"
          sx={{ ml: 2 }}
        />
      </DialogTitle>
      <DialogContent>
        <Box sx={{ mb: 2 }}>
          <TextField
            fullWidth
            label="Search Students"
            value={searchTerm}
            onChange={(e) => setSearchTerm(e.target.value)}
            margin="normal"
            variant="outlined"
            size="small"
          />
        </Box>

        <Box>
          <List>
            {filteredEnrolledStudents.map((student) => (
              <ListItem key={student.id}>
                <ListItemText
                  primary={student.name}
                  secondary={`Grade: ${student.grade}`}
                />
                <ListItemSecondaryAction>
                  <IconButton
                    edge="end"
                    onClick={() => handleUnenroll(student.id)}
                    disabled={isProcessing}
                  >
                    <Remove />
                  </IconButton>
                </ListItemSecondaryAction>
              </ListItem>
            ))}
          </List>
        </Box>

        {course && enrolledStudents.length < course.capacity && (
          <Box sx={{ mt: 2 }}>
            <Typography variant="subtitle1" sx={{ mb: 1 }}>
              Available Students
            </Typography>
            <List>
              {filteredAvailableStudents.map((student) => (
                <ListItem key={student.id}>
                  <ListItemText
                    primary={student.name}
                    secondary={`Grade: ${student.grade}`}
                  />
                  <ListItemSecondaryAction>
                    <IconButton
                      edge="end"
                      onClick={() => handleEnroll(student.id)}
                      disabled={isProcessing}
                    >
                      <Add />
                    </IconButton>
                  </ListItemSecondaryAction>
                </ListItem>
              ))}
            </List>
          </Box>
        )}
      </DialogContent>
      <DialogActions>
        <Button onClick={onClose}>Close</Button>
      </DialogActions>
    </Dialog>
  );
};

export default EnrollmentDialog;