import axios from 'axios';
import environment from '../config/environment.jsx';

// Create an instance of axios with custom configuration
const api = axios.create({
  baseURL: environment.apiUrl,
  headers: {
    'Content-Type': 'application/json',
  },
  timeout: environment.apiTimeout || 10000, // Default timeout of 10 seconds if not specified
  withCredentials: true, // Include credentials (cookies) with requests
});

// Create a fallback API instance that uses the fallback URL
const fallbackApi = axios.create({
  baseURL: environment.fallbackApiUrl,
  headers: {
    'Content-Type': 'application/json',
  },
  timeout: environment.apiTimeout,
  withCredentials: true,
});

// Track API availability to quickly switch to fallback without waiting for timeout
let isPrimaryApiAvailable = true;
let lastApiCheckTime = 0;
let isOffline = false;

// Function to check if the user is offline
const checkIsOffline = () => {
  return !navigator.onLine;
};

// Initialize offline status
isOffline = checkIsOffline();

// Listen for online/offline events
if (typeof window !== 'undefined') {
  window.addEventListener('online', () => {
    console.log(' Application is online');
    isOffline = false;
    // Reset API availability check to force a fresh health check
    lastApiCheckTime = 0;
  });

  window.addEventListener('offline', () => {
    console.log(' Application is offline');
    isOffline = true;
  });
}

// Function to check API health and determine which API instance to use
const getApiInstance = async (skipHealthCheck = false) => {
  // If offline, immediately return api instance but it will likely fail
  if (isOffline) {
    console.warn(' Device appears to be offline, but trying request anyway');
    return api;
  }
  
  // Only check API health once every minute to avoid excessive checks
  // or if explicitly requested to skip the health check
  const now = Date.now();
  if (skipHealthCheck || (now - lastApiCheckTime < 60000)) {
    return isPrimaryApiAvailable ? api : fallbackApi;
  }
  
  lastApiCheckTime = now;
  
  try {
    // Try a quick health check to the primary API
    await axios.get(`${environment.apiUrl}/health`, { 
      timeout: 2000,
      validateStatus: (status) => status < 500, // Any non-5xx response is considered "healthy"
    });
    isPrimaryApiAvailable = true;
    console.log(' Primary API is available');
    return api;
  } catch (error) {
    console.warn(' Primary API appears to be down, using fallback');
    try {
      // Also check if fallback is available
      await axios.get(`${environment.fallbackApiUrl}/health`, { 
        timeout: 2000,
        validateStatus: (status) => status < 500,
      });
      console.log(' Fallback API is available');
      isPrimaryApiAvailable = false;
      return fallbackApi;
    } catch (fallbackError) {
      console.error(' Both primary and fallback APIs are unreachable');
      // Still return fallbackApi as last resort
      isPrimaryApiAvailable = false;
      return fallbackApi;
    }
  }
};

// Helper function to retry failed requests
const retryRequest = async (config, retryCount = 0) => {
  try {
    // On first attempt with no retries yet, try with primary API or cached decision
    const apiInstance = await getApiInstance(retryCount > 0);
    return await apiInstance(config);
  } catch (error) {
    // If this was primary API and it failed with a network error, immediately try fallback
    if (retryCount === 0 && isPrimaryApiAvailable && !error.response) {
      console.log(' Primary API failed with network error, immediately trying fallback...');
      isPrimaryApiAvailable = false;
      return retryRequest(config, retryCount + 0.5); // Use 0.5 to indicate this special retry
    }
    
    // Normal retry logic for other errors
    if (retryCount < environment.retryAttempts && (!error.response || error.response.status >= 500)) {
      console.log(` Retrying request (${Math.ceil(retryCount + 1)}/${environment.retryAttempts})...`);
      // Wait a bit before retrying (exponential backoff)
      await new Promise(r => setTimeout(r, 1000 * Math.pow(2, Math.floor(retryCount))));
      return retryRequest(config, Math.ceil(retryCount + 1));
    }
    throw error;
  }
};

// Function to make API requests with fallback and retry logic
const makeRequest = async (method, url, data = null, customConfig = {}) => {
  const config = {
    method,
    url,
    ...customConfig
  };
  
  if (data) {
    config.data = data;
  }
  
  try {
    return await retryRequest(config);
  } catch (error) {
    throw error;
  }
};

// Request interceptor
api.interceptors.request.use(
  (config) => {
    // Debug logging for requests
    console.log(` API Request: ${config.method?.toUpperCase()} ${config.baseURL}${config.url}`, {
      headers: { ...config.headers, Authorization: config.headers.Authorization ? '****' : undefined },
      data: config.data,
      params: config.params,
    });
    
    // Get token and user data from localStorage
    const token = localStorage.getItem('token');
    const userData = localStorage.getItem('user');
    let userRole = null;
    
    // Try to get the user's role
    try {
      if (userData) {
        const parsedUser = JSON.parse(userData);
        userRole = parsedUser.role;
        
        // Add role to request headers for backend authorization
        if (userRole) {
          config.headers['X-User-Role'] = userRole;
        }
        
        // For write operations (POST, PUT, DELETE), add role as a query parameter as well
        if (['post', 'put', 'delete'].includes(config.method?.toLowerCase()) && userRole) {
          // Initialize params object if it doesn't exist
          config.params = config.params || {};
          // Add the role parameter
          config.params.role = userRole;
        }
      }
    } catch (e) {
      console.error(' Error parsing user data:', e);
    }
    
    if (token) {
      console.log(' Auth token found, adding to request headers');
      config.headers.Authorization = `Bearer ${token}`;
      
      // Add debugging for token inspection
      try {
        const payload = JSON.parse(atob(token.split('.')[1]));
        console.log(' Token payload inspection:', {
          exp: new Date(payload.exp * 1000).toLocaleString(),
          roles: payload.roles || payload.authorities || payload.role || 'Not found in token',
          sub: payload.sub || payload.username
        });
      } catch (e) {
        console.error(' Could not parse token for debugging:', e);
      }
    } else {
      console.warn(' No auth token found in localStorage');
    }
    
    return config;
  },
  (error) => {
    console.error(' Request interceptor error:', error);
    return Promise.reject(error);
  }
);

// Apply the same request interceptor to fallbackApi
fallbackApi.interceptors.request.use(
  api.interceptors.request.handlers[0].fulfilled,
  api.interceptors.request.handlers[0].rejected
);

// Response interceptor
api.interceptors.response.use(
  (response) => {
    // Debug logging for successful responses
    console.log(` API Response: ${response.status} ${response.config.method?.toUpperCase()} ${response.config.url}`, {
      data: response.data,
    });
    return response;
  },
  (error) => {
    // Check for network errors
    if (!error.response) {
      console.error(' Network Error:', {
        error: error.message,
        config: error.config ? {
          url: error.config.url,
          method: error.config.method?.toUpperCase(),
          baseURL: error.config.baseURL,
        } : 'No config available'
      });
      
      // Try to determine if the API server is unreachable
      const isServerUnreachable = 
        error.message.includes('Network Error') || 
        error.message.includes('timeout') ||
        error.code === 'ERR_NETWORK' ||
        error.code === 'ECONNABORTED';
      
      // Check if the user is offline
      const networkOffline = checkIsOffline();
      
      return Promise.reject({
        message: networkOffline 
          ? 'Your device appears to be offline. Please check your internet connection.'
          : isServerUnreachable 
            ? `Cannot connect to server at ${error.config?.baseURL || 'unknown URL'}. Please check if the backend server is running.`
            : 'Network error. Please check your connection.',
        status: 'network_error',
        originalError: error,
        isOffline: networkOffline
      });
    }

    // For response errors
    console.error(` API Error: ${error.response?.status}`, {
      url: error.config?.url,
      method: error.config?.method?.toUpperCase(),
      data: error.response?.data,
    });

    // Standardize error responses
    const customError = {
      message: '',
      status: error.response.status,
      originalError: error.response.data,
    };

    switch (error.response.status) {
      case 400:
        customError.message = 'Invalid request. Please check your data.';
        break;
      case 401:
        customError.message = 'Unauthorized. Please log in again.';
        
        // If token is expired or invalid, redirect to login
        if (error.response.data && (
            error.response.data.message?.toLowerCase().includes('expired') ||
            error.response.data.message?.toLowerCase().includes('invalid token')
        )) {
          console.log(' Token expired or invalid - redirecting to login');
          localStorage.removeItem('token');
          localStorage.removeItem('user');
          // Use a timeout to avoid immediate redirect that can cause refresh loops
          setTimeout(() => {
            window.location.href = '/login';
          }, 100);
        }
        break;
      case 403:
        customError.message = "Access denied. You don't have permission for this action.";
        break;
      case 404:
        customError.message = 'Resource not found.';
        break;
      case 422:
        customError.message = 'Validation error. Please check your input.';
        break;
      case 500:
        customError.message = 'Server error. Please try again later.';
        break;
      default:
        customError.message = 'An unexpected error occurred.';
    }

    // Add the original error data if available
    if (error.response.data) {
      if (typeof error.response.data === 'string') {
        try {
          const parsedError = JSON.parse(error.response.data);
          customError.message = parsedError.message || customError.message;
        } catch (e) {
          customError.message = error.response.data || customError.message;
        }
      } else {
        customError.message = error.response.data.message || customError.message;
      }
    }

    return Promise.reject(customError);
  }
);

// Apply the same response interceptor to fallbackApi
fallbackApi.interceptors.response.use(
  api.interceptors.response.handlers[0].fulfilled,
  api.interceptors.response.handlers[0].rejected
);

// Export convenience methods that use our retry and fallback logic
const apiService = {
  get: (url, config = {}) => makeRequest('get', url, null, config),
  post: (url, data, config = {}) => makeRequest('post', url, data, config),
  put: (url, data, config = {}) => makeRequest('put', url, data, config),
  patch: (url, data, config = {}) => makeRequest('patch', url, data, config),
  delete: (url, config = {}) => makeRequest('delete', url, null, config),
  
  // Also export the raw instances if needed
  api,
  fallbackApi,
  
  // Export utility functions
  getApiInstance,
  isOffline: () => isOffline
};

export default apiService;