import api from './api.jsx';

export const courseService = {
  getAll: () => api.get('/courses'),
  getById: (id) => api.get(`/courses/${id}`),
  create: (course) => api.post('/courses', course),
  update: (id, course) => api.put(`/courses/${id}`, course),
  delete: (id) => api.delete(`/courses/${id}`),
  enrollStudent: (courseId, studentId) => api.post(`/courses/${courseId}/enroll/${studentId}`),
  unenrollStudent: (courseId, studentId) => api.post(`/courses/${courseId}/unenroll/${studentId}`),
  // New methods for StudentDashboard
  getEnrolledCourses: (studentId) => api.get(`/students/${studentId}/courses`),
};