import React, { useState, useEffect } from 'react';
import {
  Dialog,
  DialogTitle,
  DialogContent,
  DialogActions,
  TextField,
  Button,
  Grid,
} from '@mui/material';
import { validateCourse } from '../../utils/courseValidation.js';

const defaultCourse = {
  name: '',
  department: '',
  teacher: '',
  credits: 0,
  capacity: 0,
  enrolled: 0,
};

const CourseDialog = ({ open, onClose, onSave, course, mode }) => {
  const [formData, setFormData] = useState(defaultCourse);
  const [errors, setErrors] = useState({});
  const [isSubmitting, setIsSubmitting] = useState(false);

  useEffect(() => {
    if (course && mode === 'edit') {
      setFormData(course);
    } else {
      setFormData(defaultCourse);
    }
    setErrors({});
  }, [course, mode, open]);

  const handleChange = (field) => (event) => {
    const value = ['credits', 'capacity', 'enrolled'].includes(field)
      ? parseInt(event.target.value) || 0
      : event.target.value;
    setFormData((prev) => ({
      ...prev,
      [field]: value,
    }));
    if (errors[field]) {
      setErrors((prev) => ({
        ...prev,
        [field]: '',
      }));
    }
  };

  const handleSubmit = async () => {
    const validationErrors = validateCourse(formData);
    if (Object.keys(validationErrors).length > 0) {
      setErrors(validationErrors);
      return;
    }

    try {
      setIsSubmitting(true);
      await onSave(formData);
      onClose();
    } catch (error) {
      console.error('Error saving course:', error);
    } finally {
      setIsSubmitting(false);
    }
  };

  return (
    <Dialog
      open={open}
      onClose={onClose}
      maxWidth="sm"
      fullWidth
      PaperProps={{
        sx: { minHeight: '400px' }
      }}
    >
      <DialogTitle>{mode === 'add' ? 'Add New Course' : 'Edit Course'}</DialogTitle>
      <DialogContent>
        <Grid container spacing={2} sx={{ mt: 1 }}>
          <Grid item xs={12}>
            <TextField
              fullWidth
              label="Course Name"
              value={formData.name}
              onChange={handleChange('name')}
              error={!!errors.name}
              helperText={errors.name}
              required
              autoFocus
            />
          </Grid>
          <Grid item xs={12}>
            <TextField
              fullWidth
              label="Department"
              value={formData.department}
              onChange={handleChange('department')}
              error={!!errors.department}
              helperText={errors.department}
              required
            />
          </Grid>
          <Grid item xs={12}>
            <TextField
              fullWidth
              label="Teacher"
              value={formData.teacher}
              onChange={handleChange('teacher')}
              error={!!errors.teacher}
              helperText={errors.teacher}
              required
            />
          </Grid>
          <Grid item xs={6}>
            <TextField
              fullWidth
              label="Credits"
              type="number"
              value={formData.credits}
              onChange={handleChange('credits')}
              error={!!errors.credits}
              helperText={errors.credits}
              required
              inputProps={{ min: 0 }}
            />
          </Grid>
          <Grid item xs={6}>
            <TextField
              fullWidth
              label="Capacity"
              type="number"
              value={formData.capacity}
              onChange={handleChange('capacity')}
              error={!!errors.capacity}
              helperText={errors.capacity}
              required
              inputProps={{ min: 0 }}
            />
          </Grid>
          {mode === 'edit' && (
            <Grid item xs={6}>
              <TextField
                fullWidth
                label="Current Enrollment"
                type="number"
                value={formData.enrolled}
                onChange={handleChange('enrolled')}
                error={!!errors.enrolled}
                helperText={errors.enrolled}
                inputProps={{ min: 0, max: formData.capacity }}
              />
            </Grid>
          )}
        </Grid>
      </DialogContent>
      <DialogActions>
        <Button onClick={onClose} disabled={isSubmitting}>
          Cancel
        </Button>
        <Button
          onClick={handleSubmit}
          variant="contained"
          color="primary"
          disabled={isSubmitting}
        >
          {isSubmitting ? 'Saving...' : mode === 'add' ? 'Add' : 'Save'}
        </Button>
      </DialogActions>
    </Dialog>
  );
};

export default CourseDialog;