import React, { useState, useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import {
  Box,
  Typography,
  Grid,
  Card,
  CardContent,
  CardActions,
  Button,
  Paper,
  List,
  ListItem,
  ListItemIcon,
  ListItemText,
  Divider,
} from '@mui/material';
import {
  People,
  School,
  Book,
  Group,
  Warning,
  ArrowUpward,
  Refresh,
  Person,
  Assignment,
  EventNote,
} from '@mui/icons-material';
import { studentService } from '../services/studentService.jsx';
import { teacherService } from '../services/teacherService.jsx';
import { courseService } from '../services/courseService.jsx';
import Loading from '../components/Loading.jsx';
import ErrorMessage from '../components/ErrorMessage.jsx';
import { useNotification } from '../context/NotificationContext';
import { useAuth } from '../context/AuthContext';

// Define roles constant since we're in the jsx file
const ROLES = {
  ADMIN: 'ADMIN',
  TEACHER: 'TEACHER',
  STAFF: 'STAFF',
  PARENT: 'PARENT',
  STUDENT: 'STUDENT',
};

// StatCard component remains the same
const StatCard = ({ title, value, icon: Icon, color, onClick }) => (
  <Card
    sx={{
      height: '100%',
      display: 'flex',
      flexDirection: 'column',
      cursor: onClick ? 'pointer' : 'default',
      transition: 'transform 0.2s',
      '&:hover': onClick ? {
        transform: 'translateY(-4px)',
        boxShadow: 4,
      } : {},
    }}
    onClick={onClick}
  >
    <CardContent sx={{ flexGrow: 1 }}>
      <Box sx={{ display: 'flex', alignItems: 'center', mb: 2 }}>
        <Icon sx={{ color }} />
        <Typography variant="h6" component="div" sx={{ ml: 1 }}>
          {title}
        </Typography>
      </Box>
      <Typography variant="h4" color={color}>
        {value}
      </Typography>
    </CardContent>
    {onClick && (
      <CardActions>
        <Button size="small" endIcon={<ArrowUpward />}>
          View Details
        </Button>
      </CardActions>
    )}
  </Card>
);

// Student Dashboard Component
const StudentDashboard = () => {
  const navigate = useNavigate();
  const { user } = useAuth();
  const { showNotification } = useNotification();
  const [studentData, setStudentData] = useState({
    studentInfo: null,
    enrolledCourses: [],
    upcomingAssignments: [],
    attendance: { present: 0, absent: 0, late: 0, percentage: 0 }
  });
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);

  const loadStudentData = async () => {
    try {
      setLoading(true);
      setError(null);
      
      console.log("StudentDashboard: Fetching data from backend API");
      
      // We'll need to get the student ID from the user object
      const studentId = user.id || user.studentId; // Adjust based on your user object structure
      
      if (!studentId) {
        console.warn("StudentDashboard: No student ID found in user object:", user);
      }
      
      // Making parallel requests to get all necessary student data
      // Adjust these API calls according to your actual backend endpoints
      const [enrolledCoursesRes, assignmentsRes, attendanceRes] = await Promise.all([
        // Get courses the student is enrolled in
        courseService.getEnrolledCourses(studentId),
        // Get upcoming assignments for the student
        studentService.getAssignments(studentId),
        // Get attendance records
        studentService.getAttendance(studentId)
      ]);
      
      console.log("StudentDashboard: Data loaded successfully", {
        courses: enrolledCoursesRes?.data?.length,
        assignments: assignmentsRes?.data?.length,
        attendance: !!attendanceRes?.data
      });
      
      // Process attendance data to calculate percentage
      const attendanceData = attendanceRes.data || {};
      const present = attendanceData.present || 0;
      const absent = attendanceData.absent || 0;
      const late = attendanceData.late || 0;
      const total = present + absent + late;
      const percentage = total > 0 ? Math.round((present / total) * 100) : 0;
      
      setStudentData({
        studentInfo: user,
        enrolledCourses: enrolledCoursesRes.data || [],
        upcomingAssignments: assignmentsRes.data || [],
        attendance: {
          present,
          absent,
          late,
          percentage
        }
      });
    } catch (error) {
      console.error('StudentDashboard: Error loading dashboard data:', error);
      
      // If API endpoints aren't available yet, show a clear error
      if (error.response && error.response.status === 404) {
        setError('Student API endpoints not available. Please contact your administrator.');
      } else {
        setError('Failed to load student dashboard data. Please check your connection.');
      }
      
      showNotification({
        type: 'error',
        message: 'Could not load student dashboard data from server.',
      });
    } finally {
      setLoading(false);
    }
  };

  useEffect(() => {
    console.log("StudentDashboard: Component mounted, loading data from API");
    loadStudentData();
    const interval = setInterval(loadStudentData, 5 * 60 * 1000); // Refresh every 5 minutes
    return () => clearInterval(interval);
  }, []);

  if (loading) {
    return <Loading message="Loading your dashboard data from server..." />;
  }

  if (error) {
    return <ErrorMessage message={error} onRetry={loadStudentData} />;
  }

  return (
    <Box>
      <Typography variant="h4" sx={{ mb: 4 }}>
        Student Dashboard
        {user && <Typography variant="subtitle1" color="text.secondary">Welcome, {user.name || user.username}</Typography>}
      </Typography>

      <Grid container spacing={3}>
        <Grid item xs={12} sm={6} md={3}>
          <StatCard
            title="Enrolled Courses"
            value={studentData.enrolledCourses.length}
            icon={Book}
            color="primary.main"
            onClick={() => navigate('/courses')}
          />
        </Grid>
        <Grid item xs={12} sm={6} md={3}>
          <StatCard
            title="Upcoming Assignments"
            value={studentData.upcomingAssignments.length}
            icon={Assignment}
            color="secondary.main"
          />
        </Grid>
        <Grid item xs={12} sm={6} md={3}>
          <StatCard
            title="Attendance Rate"
            value={`${studentData.attendance.percentage}%`}
            icon={EventNote}
            color="success.main"
          />
        </Grid>
        <Grid item xs={12} sm={6} md={3}>
          <StatCard
            title="GPA"
            value="3.5"
            icon={School}
            color="info.main"
          />
        </Grid>

        {/* Enrolled Courses */}
        <Grid item xs={12} md={6}>
          <Paper sx={{ p: 2, height: '100%' }}>
            <Typography variant="h6" gutterBottom>
              Your Courses
            </Typography>
            <List>
              {studentData.enrolledCourses.map((course, index) => (
                <React.Fragment key={course.id}>
                  {index > 0 && <Divider />}
                  <ListItem>
                    <ListItemIcon>
                      <Book color="primary" />
                    </ListItemIcon>
                    <ListItemText
                      primary={course.name}
                      secondary={
                        <>
                          {course.teacher}  {course.schedule}
                          {course.grade && (
                            <Typography component="span" color="text.secondary">
                              {"  Grade: "}
                              <Typography component="span" color="primary.main" fontWeight="bold">
                                {course.grade}
                              </Typography>
                            </Typography>
                          )}
                        </>
                      }
                    />
                  </ListItem>
                </React.Fragment>
              ))}
            </List>
            <Box sx={{ mt: 2, display: 'flex', justifyContent: 'flex-end' }}>
              <Button size="small" color="primary" onClick={() => navigate('/courses')}>
                View All Courses
              </Button>
            </Box>
          </Paper>
        </Grid>

        {/* Upcoming Assignments */}
        <Grid item xs={12} md={6}>
          <Paper sx={{ p: 2, height: '100%' }}>
            <Typography variant="h6" gutterBottom>
              Upcoming Assignments
            </Typography>
            <List>
              {studentData.upcomingAssignments.map((assignment, index) => (
                <React.Fragment key={assignment.id}>
                  {index > 0 && <Divider />}
                  <ListItem>
                    <ListItemIcon>
                      <Assignment color="secondary" />
                    </ListItemIcon>
                    <ListItemText
                      primary={assignment.title}
                      secondary={
                        <>
                          Due: {assignment.dueDate}  {assignment.course}
                        </>
                      }
                    />
                  </ListItem>
                </React.Fragment>
              ))}
            </List>
          </Paper>
        </Grid>

        {/* Attendance Summary */}
        <Grid item xs={12}>
          <Paper sx={{ p: 2 }}>
            <Typography variant="h6" gutterBottom>
              Attendance Summary
            </Typography>
            <Grid container spacing={3} sx={{ mt: 1 }}>
              <Grid item xs={12} sm={4}>
                <Box sx={{ textAlign: 'center', p: 2 }}>
                  <Typography variant="h2" color="primary.main">
                    {studentData.attendance.present}
                  </Typography>
                  <Typography variant="body1">Days Present</Typography>
                </Box>
              </Grid>
              <Grid item xs={6} sm={4}>
                <Box sx={{ textAlign: 'center', p: 2 }}>
                  <Typography variant="h2" color="error.main">
                    {studentData.attendance.absent}
                  </Typography>
                  <Typography variant="body1">Days Absent</Typography>
                </Box>
              </Grid>
              <Grid item xs={6} sm={4}>
                <Box sx={{ textAlign: 'center', p: 2 }}>
                  <Typography variant="h2" color="warning.main">
                    {studentData.attendance.late}
                  </Typography>
                  <Typography variant="body1">Days Late</Typography>
                </Box>
              </Grid>
            </Grid>
          </Paper>
        </Grid>
      </Grid>
    </Box>
  );
};

// Admin Dashboard component
const AdminDashboard = () => {
  const navigate = useNavigate();
  const { showNotification } = useNotification();
  const [stats, setStats] = useState({
    totalStudents: 0,
    totalTeachers: 0,
    totalCourses: 0,
    activeEnrollments: 0,
    averageClassSize: 0,
    coursesAtCapacity: 0,
    recentActivity: [],
  });
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);

  const loadStats = async () => {
    try {
      setLoading(true);
      setError(null);
      
      console.log("AdminDashboard: Fetching data from backend API");
      
      // Fetch data from API with individual try/catch blocks to maintain the dashboard structure
      // even if some data fails to load
      let studentsData = [], teachersData = [], coursesData = [];
      let apiErrors = [];
      
      try {
        const studentsResponse = await studentService.getAll();
        studentsData = studentsResponse.data || [];
        console.log("AdminDashboard: Students data loaded:", studentsData.length);
      } catch (err) {
        console.error("AdminDashboard: Failed to load students", err);
        apiErrors.push("students");
      }
      
      try {
        const teachersResponse = await teacherService.getAll();
        teachersData = teachersResponse.data || [];
        console.log("AdminDashboard: Teachers data loaded:", teachersData.length);
      } catch (err) {
        console.error("AdminDashboard: Failed to load teachers", err);
        apiErrors.push("teachers");
      }
      
      try {
        const coursesResponse = await courseService.getAll();
        coursesData = coursesResponse.data || [];
        console.log("AdminDashboard: Courses data loaded:", coursesData.length);
      } catch (err) {
        console.error("AdminDashboard: Failed to load courses", err);
        apiErrors.push("courses");
      }
      
      // If we have API errors, show a warning but still display the dashboard
      if (apiErrors.length > 0) {
        showNotification({
          type: 'warning',
          message: `Some data could not be loaded: ${apiErrors.join(', ')}`,
        });
      }

      // Calculate derived statistics from available data
      const totalEnrollments = coursesData.reduce(
        (sum, course) => sum + (course.enrolled || 0), 
        0
      );
      
      const avgClassSize = coursesData.length > 0
        ? totalEnrollments / coursesData.length
        : 0;
        
      const coursesAtCapacity = coursesData.filter(
        course => (course.enrolled || 0) >= (course.capacity || 0)
      ).length;

      // Generate activity feed from available data
      const recentActivity = [];
      
      // Add course activities if we have course data
      if (coursesData.length > 0) {
        coursesData.slice(0, 3).forEach(course => {
          recentActivity.push({
            id: `course-${course.id}`,
            type: 'course',
            description: `Course ${course.name} has ${(course.capacity || 0) - (course.enrolled || 0)} seats remaining`,
            timestamp: new Date().toISOString()
          });
        });
      }
      
      // Add student activities if we have student data
      if (studentsData.length > 0) {
        studentsData.slice(0, 3).forEach((student, idx) => {
          recentActivity.push({
            id: `student-${student.id || idx}`,
            type: 'student',
            description: `Student ${student.name || 'Unknown'} - Grade ${student.grade || 'N/A'}`,
            timestamp: new Date(Date.now() - (idx + 1) * 3600000).toISOString()
          });
        });
      }
      
      // Sort activities by timestamp
      recentActivity.sort((a, b) => 
        new Date(b.timestamp).getTime() - new Date(a.timestamp).getTime()
      );

      // Update state with all data we've collected
      setStats({
        totalStudents: studentsData.length,
        totalTeachers: teachersData.length,
        totalCourses: coursesData.length,
        activeEnrollments: totalEnrollments,
        averageClassSize: avgClassSize.toFixed(1),
        coursesAtCapacity,
        recentActivity,
      });
      
    } catch (error) {
      console.error('AdminDashboard: Error loading dashboard stats:', error);
      setError('Failed to load dashboard statistics');
      showNotification({
        type: 'error',
        message: 'Failed to load dashboard statistics. Please check API connection.',
      });
    } finally {
      setLoading(false);
    }
  };

  useEffect(() => {
    console.log("AdminDashboard: Component mounted, loading statistics from API");
    loadStats();
    const interval = setInterval(loadStats, 5 * 60 * 1000); // Refresh every 5 minutes
    return () => clearInterval(interval);
  }, []);

  if (loading) {
    return <Loading message="Loading dashboard statistics..." />;
  }

  // Even with errors, we show the dashboard UI with the data we have
  return (
    <Box>
      <Typography variant="h4" sx={{ mb: 4 }}>
        Admin Dashboard
      </Typography>

      <Grid container spacing={3}>
        <Grid item xs={12} sm={6} md={3}>
          <StatCard
            title="Total Students"
            value={stats.totalStudents}
            icon={People}
            color="primary.main"
            onClick={() => navigate('/students')}
          />
        </Grid>
        <Grid item xs={12} sm={6} md={3}>
          <StatCard
            title="Total Teachers"
            value={stats.totalTeachers}
            icon={School}
            color="secondary.main"
            onClick={() => navigate('/teachers')}
          />
        </Grid>
        <Grid item xs={12} sm={6} md={3}>
          <StatCard
            title="Total Courses"
            value={stats.totalCourses}
            icon={Book}
            color="success.main"
            onClick={() => navigate('/courses')}
          />
        </Grid>
        <Grid item xs={12} sm={6} md={3}>
          <StatCard
            title="Active Enrollments"
            value={stats.activeEnrollments}
            icon={People}
            color="info.main"
          />
        </Grid>
        <Grid item xs={12} sm={6} md={3}>
          <StatCard
            title="Average Class Size"
            value={stats.averageClassSize}
            icon={Group}
            color="warning.main"
          />
        </Grid>
        <Grid item xs={12} sm={6} md={3}>
          <StatCard
            title="Courses at Capacity"
            value={stats.coursesAtCapacity}
            icon={Warning}
            color="error.main"
            onClick={() => navigate('/courses')}
          />
        </Grid>

        <Grid item xs={12}>
          <Paper sx={{ p: 2, mt: 2 }}>
            <Box sx={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center', mb: 2 }}>
              <Typography variant="h6">Recent Activity</Typography>
              <Button size="small" startIcon={<Refresh />} onClick={loadStats}>
                Refresh
              </Button>
            </Box>
            {error ? (
              <Typography color="error" sx={{ textAlign: 'center', py: 2 }}>
                {error}
                <Button variant="text" color="primary" onClick={loadStats} sx={{ ml: 2 }}>
                  Retry
                </Button>
              </Typography>
            ) : stats.recentActivity.length === 0 ? (
              <Typography sx={{ textAlign: 'center', py: 2 }}>
                No recent activity to display
              </Typography>
            ) : (
              <List>
                {stats.recentActivity.map((activity, index) => (
                  <React.Fragment key={activity.id || index}>
                    {index > 0 && <Divider />}
                    <ListItem>
                      <ListItemIcon>
                        {activity.type === 'enrollment' && <People color="primary" />}
                        {activity.type === 'course' && <Book color="secondary" />}
                        {activity.type === 'student' && <School color="success" />}
                        {activity.type === 'teacher' && <Person color="info" />}
                      </ListItemIcon>
                      <ListItemText
                        primary={activity.description}
                        secondary={new Date(activity.timestamp).toLocaleString()}
                      />
                    </ListItem>
                  </React.Fragment>
                ))}
              </List>
            )}
          </Paper>
        </Grid>
      </Grid>
    </Box>
  );
};

// Main Dashboard component that renders the appropriate dashboard based on user role
const Dashboard = () => {
  const { user } = useAuth();
  
  console.log("Dashboard: Rendering dashboard component with user:", user);
  
  // If user is not available yet, show loading
  if (!user) {
    console.log("Dashboard: No user data available");
    return <Loading message="Loading user data..." />;
  }
  
  // Check if we have a role directly on the user object
  let role = user.role;
  
  // If we have a user but role is undefined, we need to log out and show error
  if (!role) {
    console.error("Dashboard: User object missing role property:", user);
    return (
      <Box 
        sx={{ 
          display: 'flex',
          flexDirection: 'column',
          alignItems: 'center',
          justifyContent: 'center',
          height: 'calc(100vh - 130px)',
          width: '100%',
        }}
      >
        <Paper 
          elevation={3} 
          sx={{
            p: 4,
            borderRadius: 2,
            maxWidth: 500,
            width: '100%',
            textAlign: 'center',
          }}
        >
          <Warning sx={{ fontSize: 64, color: 'error.main', mb: 3 }} />
          <Typography variant="h4" color="text.primary" gutterBottom>
            Authentication Error
          </Typography>
          <Typography variant="h6" color="text.secondary" paragraph>
            Invalid user data received. Please log out and log in again.
          </Typography>
          <Box sx={{ display: 'flex', justifyContent: 'center', gap: 2 }}>
            <Button 
              variant="contained" 
              color="primary"
              onClick={() => window.location.reload()}
            >
              Refresh Page
            </Button>
            <Button 
              variant="outlined" 
              color="secondary"
              onClick={() => {
                // Use the logout function from useAuth
                const { logout } = useAuth();
                if (logout) logout();
                else window.location.href = '/login';
              }}
            >
              Log Out
            </Button>
          </Box>
        </Paper>
      </Box>
    );
  }
  
  // Normalize the role for comparison
  const userRole = role.toUpperCase();
  console.log("Dashboard: User role (normalized):", userRole);
  
  if (userRole === 'ADMIN' || userRole === 'STAFF') {
    console.log("Dashboard: Showing AdminDashboard for role:", userRole);
    return <AdminDashboard />;
  } 
  
  if (userRole === 'TEACHER') {
    console.log("Dashboard: Showing TeacherDashboard (AdminDashboard) for role:", userRole);
    return <AdminDashboard />;
  }
  
  if (userRole === 'STUDENT') {
    console.log("Dashboard: Showing StudentDashboard for role:", userRole);
    return <StudentDashboard />;
  }
  
  if (userRole === 'PARENT') {
    console.log("Dashboard: Showing ParentDashboard (StudentDashboard) for role:", userRole);
    return <StudentDashboard />;
  }

  // If we get here, the role didn't match any expected values
  console.log("Dashboard: No matching role found. Current role:", userRole);
  return (
    <Box 
      sx={{ 
        display: 'flex',
        flexDirection: 'column',
        alignItems: 'center',
        justifyContent: 'center',
        height: 'calc(100vh - 130px)',
        width: '100%',
      }}
    >
      <Paper 
        elevation={3} 
        sx={{
          p: 4,
          borderRadius: 2,
          maxWidth: 500,
          width: '100%',
          textAlign: 'center',
        }}
      >
        <Warning sx={{ fontSize: 64, color: 'warning.main', mb: 3 }} />
        <Typography variant="h4" color="text.primary" gutterBottom>
          Dashboard Access Restricted
        </Typography>
        <Typography variant="h6" color="text.secondary" paragraph>
          Dashboard not available for role: {userRole}
        </Typography>
        <Typography variant="body1" color="text.secondary" sx={{ mb: 3 }}>
          Please contact the system administrator if you believe this is an error.
        </Typography>
        <Button 
          variant="contained" 
          color="primary"
          onClick={() => window.location.reload()}
          sx={{ minWidth: 150 }}
        >
          Refresh Page
        </Button>
      </Paper>
    </Box>
  );
};

export default Dashboard;