// Direct JSX implementation for browser compatibility
import React, { createContext, useContext, useState, useEffect, useCallback } from 'react';
import { useNavigate } from 'react-router-dom';
import { authService } from '../services/authService';
import { useNotification } from './NotificationContext.js';

const AuthContext = createContext(undefined);

// Inactivity timeout in milliseconds (10 minutes)
const INACTIVITY_TIMEOUT = 10 * 60 * 1000;

export const AuthProvider = ({ children }) => {
  const [user, setUser] = useState(null);
  const [isAuthenticated, setIsAuthenticated] = useState(false);
  const [loading, setLoading] = useState(true);
  const [inactivityTimer, setInactivityTimer] = useState(null);
  const navigate = useNavigate();
  const { showNotification } = useNotification();

  // Function to reset the inactivity timer
  const resetInactivityTimer = useCallback(() => {
    // Clear any existing timer
    if (inactivityTimer) {
      clearTimeout(inactivityTimer);
    }
    
    // Only set new timer if user is authenticated
    if (isAuthenticated) {
      // Set a new timer
      const newTimer = setTimeout(() => {
        // Log out the user when timer expires
        logout('You have been logged out due to inactivity');
      }, INACTIVITY_TIMEOUT);
      
      setInactivityTimer(newTimer);
    }
  // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [isAuthenticated]); // Remove inactivityTimer from dependencies to break the infinite loop

  // Check if token is valid on initial load and setup activity listeners
  useEffect(() => {
    const checkAuth = async () => {
      console.log("AuthContext: Checking authentication...");
      const token = localStorage.getItem('token');
      const userData = localStorage.getItem('user');
      
      console.log("AuthContext: Retrieved from localStorage:", { token: !!token, userData: !!userData });
      
      if (!token || !userData) {
        console.log("AuthContext: No token or user data found in localStorage");
        setLoading(false);
        return;
      }
      
      try {
        // Parse user data from localStorage
        const parsedUserData = JSON.parse(userData);
        console.log("AuthContext: Parsed user data:", parsedUserData);
        console.log("AuthContext: User role from localStorage:", parsedUserData.role);
        
        // Update state with user info
        setUser(parsedUserData);
        setIsAuthenticated(true);
        resetInactivityTimer();
        
        // Log the user state after setting
        console.log("AuthContext: User state updated:", {
          user: parsedUserData,
          isAuthenticated: true
        });
      } catch (error) {
        console.error('AuthContext: Token validation failed:', error);
        // Clear invalid session data
        localStorage.removeItem('token');
        localStorage.removeItem('user');
      } finally {
        setLoading(false);
      }
    };
    
    checkAuth();
    
    // Setup event listeners to track user activity
    const activityEvents = ['mousedown', 'keydown', 'touchstart', 'scroll'];
    const handleUserActivity = () => resetInactivityTimer();
    
    activityEvents.forEach(event => {
      window.addEventListener(event, handleUserActivity);
    });
    
    // Cleanup event listeners on unmount
    return () => {
      activityEvents.forEach(event => {
        window.removeEventListener(event, handleUserActivity);
      });
      
      if (inactivityTimer) {
        clearTimeout(inactivityTimer);
      }
    };
  }, [resetInactivityTimer]);

  const login = async (username, password) => {
    try {
      console.log("AuthContext: Attempting login for username:", username);
      
      const response = await authService.login({ username, password });
      console.log("AuthContext: Raw API response received");
      
      // Extract user data from the nested structure
      // Looking at your error message, we can see the data is nested: response.data.token, response.data.username, etc.
      const userData = response.data;
      console.log("AuthContext: Extracted user data:", userData);
      
      // Validate the required fields
      if (!userData || !userData.token) {
        console.error("AuthContext: Invalid user data or missing token", userData);
        throw new Error("Login failed: Missing token in response");
      }
      
      // Validate role is present
      if (!userData.role) {
        console.error("AuthContext: Missing role in user data", userData);
        throw new Error("Login failed: User role not specified");
      }
      
      console.log("AuthContext: Valid user data with role:", userData.role);
      
      // Store clean data in localStorage - just the user data, not the full response
      localStorage.setItem('token', userData.token);
      localStorage.setItem('user', JSON.stringify(userData));
      
      // Update application state with just the user data
      setUser(userData);
      setIsAuthenticated(true);
      resetInactivityTimer();
      
      navigate('/dashboard');
      showNotification({
        type: 'success',
        message: 'Login successful',
      });
    } catch (error) {
      console.error('AuthContext: Login error:', error);
      showNotification({
        type: 'error',
        message: error.message || 'Login failed. Please check your credentials.',
      });
      throw error;
    }
  };

  const logout = (message = 'You have been logged out') => {
    console.log("AuthContext: Logging out user");
    localStorage.removeItem('token');
    localStorage.removeItem('user');
    setUser(null);
    setIsAuthenticated(false);
    
    // Clear the inactivity timer
    if (inactivityTimer) {
      clearTimeout(inactivityTimer);
      setInactivityTimer(null);
    }
    
    navigate('/login');
    showNotification({
      type: 'info',
      message: message,
    });
  };

  // Debug log the current user state whenever it changes
  useEffect(() => {
    console.log("AuthContext: Current user state:", user);
  }, [user]);

  return (
    <AuthContext.Provider value={{ user, login, logout, isAuthenticated, loading, resetInactivityTimer }}>
      {children}
    </AuthContext.Provider>
  );
};

export const useAuth = () => {
  const context = useContext(AuthContext);
  if (context === undefined) {
    throw new Error('useAuth must be used within an AuthProvider');
  }
  return context;
};

// Export both named exports and default export
export default {
  AuthProvider,
  useAuth
};